<template>
  <div>
    <el-card class="box-card">
      <el-row>
       <el-button type="primary" 
       @click="multiDel"
       icon="el-icon-delete">批量删除</el-button>
        <b>消费商户：</b>
<el-input
 v-model="input"
  placeholder="请输入消费商户"
  style="width: 150px;"
  ></el-input>
<el-button type="primary" @click="searchFun" icon="el-icon-search">搜索</el-button>
        <el-button type="primary" 
        @click="reset"
        icon="el-icon-magic-stick">重置</el-button>
      </el-row>
 <template>
  <el-table
    :data="tableData"
    border
    style="width: 100%"
    max-height="500"
    @selection-change="handleSelectionChange"
    >
     <el-table-column
      type="selection"
      width="55">
    </el-table-column>
    <el-table-column
        prop="id"
        label="ID"
        width="180"
        >
      </el-table-column>
      <el-table-column
        prop="money"
        label="消费金额"
        width="180">
      </el-table-column>
      <el-table-column
        prop="date"
        label="消费时间"
        width="180">
      </el-table-column>
      <el-table-column
        prop="name"
        label="消费商户">
      </el-table-column>
      <el-table-column 
      label="操作"
      fixed="right"
      >
      <template slot-scope="scope">
        <el-button
          size="mini"
          type="danger"
          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        
      </template>
    </el-table-column>
  </el-table>

  <div class="block">
    <el-pagination
     @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="pagesizes"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total">
    </el-pagination>
  </div>
</template>
</el-card>

    
  </div>
</template>

<script>
import API from "../../utils/service"
import axios from 'axios'
export default {
   data() {
        return {
          name: '一卡通信息',
          tableData: [],
          input: '',
          currentPage: 1,
          total: 0,
          pagesize: 5,
          pagesizes: [3,4,5,6],
          ids: [],
          card: {
            id: 0,
            money: '10',
            date: '2023-1-1',
            name: '小吃摊'
          },
          multipleSelection: []
        }
      },
  mounted(){
    this.$bus.$emit('receiveParams',this.name)
    this.init()
    this.pages(1,this.pagesize)
  },
  methods: {
      handleCurrentChange(val) {
        console.log(val);
        this.pages(val,this.pagesize)
      },
       handleSizeChange(val) {
        this.pagesize = val
        this.pages(this.currentPage,this.pagesize)
      },
    init(){
      API({
        url:'/cardController/getCard',
        method: 'get',
      }).then((res)=>{
        this.total = res.data.data.length
      })
    },
    pages(val,page) {
      // console.log('查询页码:',val,'页数:',page);
      API({
        url:`/cardController/getCard/${val}/${page}`,
        method: 'get',
      }).then((response)=>{
        this.tableData = response.data.data.records
        })
    },
      handleDelete(index, row) {
         this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          let ids = []
        ids.push(row.id)
        this.ids = ids
        API({
        url:'/cardController/deleteCard',
        data: this.ids,
        method: 'delete',
      }).then(res=>{
          this.init()
          this.handleCurrentChange(this.currentPage) 
          this.ids = []
        })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
      },
    searchFun() {
      API({
        url:`/cardController/getCard/1/${this.total}`,
        params: {
          gradeName: this.input
        },
        method: 'get',
      }).then((res)=>{
        this.input = ''
        this.tableData = res.data.data.records
      })

    },
    reset() {
      this.init()
      this.pages(1,this.pagesize)
    },
    handleSelectionChange(val) {
        this.multipleSelection = val;
        this.ids = []
        this.multipleSelection.map(item=>{
          console.log(item.id);
          this.ids.push(item.id)
        })
    },
    multiDel(){
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
        API({
        url:'/cardController/deleteCard',
        data: this.ids,
        method: 'delete',
      }).then(res=>{
          this.init()
          this.handleCurrentChange(this.currentPage) 
          this.ids = []
        })
          this.$message({
            type: 'success',
            message: '删除成功!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });
    }

    
  }
}
</script>

<style lang="scss" scoped>
  .el-row{
    margin: 30px 0 50px 0;
  }
  .text {
    font-size: 20px;
  }

  // .item {
  //   padding: 18px 0;
  // }

  .box-card {
    width: 100%;
  }

  .el-card{
    padding: 0px;
  }

  .el-button--primary {
    background-color: #FAB6B6;
    border-color: #FAB6B6;
  }

  .el-button--primary:focus, .el-button--primary:hover {
    background: #e9baba;
    border-color: #f3dfdf;
    color: #FFF;
}

  ::v-deep .el-pagination.is-background .el-pager li:not(.disabled).active {
	background-color: #f3dfdf;
}



</style>